import { Info } from '@mui/icons-material';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import useCheckScreenWidth from 'Global/Hooks/useCheckScreenWidth';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import React from 'react';
import { useNavigate } from 'react-router-dom';

const SideBarContent = ({
  route,
  image,
  title = '',
  icon,
}) => {
  const screenWidth = useCheckScreenWidth(); // 1024 for Desktop
  const navigate = useNavigate();
  const placeholder = FindLangKey(title);

  return (
    <React.Fragment>
      <div
        style={{
          width: '100%',
          color: `${themeColors.ColorPrimary} !important`,
          overflowY: screenWidth < 1400 ? 'visible' : '',
          maxHeight: screenWidth < 1400 ? '20em' : '',
        }}
      >
        <span
          onClick={() => navigate(`${route}`)}
          style={{
            display: 'flex',
            flexDirection: 'row-reverse',
            alignItems: 'center',
            gap: '.5em',
            cursor: 'pointer',
            alignItems: 'center',
          }}
        >
          {image ? (
            <img
              src={image}
              alt={placeholder}
              width="25em"
              height="25em"
              style={{
                background: image,
                cursor: 'pointer',
                backgroundPosition: 'center center',
                backgroundSize: 'cover',
              }}
            />
          ) : (
            icon
          )}

          <p
            style={{
              fontSize: '1.5em !important',
              color: `${themeColors.ColorPrimary} !important`,
            }}
          >
            {placeholder}
          </p>
        </span>
      </div>
    </React.Fragment>
  );
};

export default SideBarContent;
