import { Button, CssBaseline, Stack } from '@mui/material';
import React, { useCallback, useEffect, useMemo } from 'react';
import { DrawerLinks } from '../variables/DrawerLinks';
import LogOutButton from './LogOutButton';
import ImageUploader from './ImageUploader';
import UserGuidFetchButton from './UserGuidFetchButton';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import { useContext } from 'react';
import DrawerMenuLinkCreator from './DrawerMenuLinkCreator';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import {
  ContactEmergency,
  Info,
  Settings,
} from '@mui/icons-material';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import Cookies from 'js-cookie';
import { IconAtom } from 'Global/Hooks/useCacheIcons/IconAtom';
import { useAtom } from 'jotai';


const MainMenuDrawerOptions = ({
  setDrawerOpen,
  setRefreshInterface,
  setRefreshForImage,
  screenWidth,
}) => {
  const isDesktop = screenWidth > 1024;

  const rootPaths = useContext(GlobalContext)?.rootPaths;
  const navigate = useContext(GlobalContext)?.navigate;
  // const setSettingDrawerOpen =
  //   useContext(GlobalContext)?.setSettingDrawerOpen;
  const about_us = FindLangKey('general-public-about-us');
  const contact_us = FindLangKey(
    'general-public-contact-us'
  );

  const user = Cookies.get('user')
    ? JSON.parse(Cookies.get('user'))
    : null;

  const isPatient =
    user?.AccessRole?.map((role) =>
      role?.toLowerCase()
    )?.includes('patient') &&
    user?.AccessRole?.length === 1;


      const [icons] = useAtom(IconAtom);
  const cachedIcons =
    icons || JSON.parse(localStorage.getItem('icons'));

  const AbouUS = 'data:image/svg+xml;base64,' +
  cachedIcons?.MenueIcon['AbouUS.svg'];
  const contactUS = 'data:image/svg+xml;base64,' +
  cachedIcons?.MenueIcon['contactUS.svg'];
  const Exite = 'data:image/svg+xml;base64,' +
  cachedIcons?.MenueIcon['Exite.svg'];


  const _drawerLinks = useCallback(()=>DrawerLinks(
    isPatient,
    rootPaths,
    screenWidth,
    isDesktop,
    cachedIcons
  ),[isPatient,
    rootPaths,
    screenWidth,
    isDesktop,
    cachedIcons,
    localStorage.getItem('languageArray')
  ])

  return (
    <React.Fragment>
      <CssBaseline />
      <div
        style={{
          width: '100%',
          color: `${themeColors.ColorPrimary} !important`,
          overflowY: screenWidth < 1400 ? 'visible' : '',
        }}
      >
        <Stack gap="1em">
          <ImageUploader
            setRefreshInterface={setRefreshInterface}
            setRefreshForImage={setRefreshForImage}
            cachedIcons={cachedIcons}
          />
          <DrawerMenuLinkCreator
            Links={_drawerLinks()}
            AccessRoles={rootPaths}
          />

          {/* <span
            onClick={() => setSettingDrawerOpen(true)}
            style={{
              display: 'flex',
              flexDirection: 'row-reverse',
              alignItems: 'center',
              gap: '.5em',
              cursor: 'pointer',
            }}
          >
            <Settings
              color="colorPrimer"
              sx={{
                fontSize: '1.5em !important',
              }}
            />
            {FindLangKey(
              'personalization-drawer-navbar-title'
            )}
          </span> */}

          <UserGuidFetchButton cachedIcons={cachedIcons}/>
          <span
            onClick={() =>
              navigate(isDesktop ? '/main/about' : '/about')
            }
            style={{
              display: 'flex',
              flexDirection: 'row-reverse',
              alignItems: 'center',
              gap: '.5em',
              cursor: 'pointer',
            }}
          >
            <img
            src={AbouUS}
            alt={about_us}
            width="25em"
            height="25em"
            style={{
              background: AbouUS,
              cursor: 'pointer',
              backgroundPosition: 'center center',
              backgroundSize: 'cover',
            }}
          />
            {about_us}
          </span>
          <span
            onClick={() =>
              navigate(
                isDesktop ? '/main/ContactUs' : '/ContactUs'
              )
            }
            style={{
              display: 'flex',
              flexDirection: 'row-reverse',
              alignItems: 'center',
              gap: '.5em',
              cursor: 'pointer',
            }}
          >
              <img
            src={contactUS}
            alt={contact_us}
            width="25em"
            height="25em"
            style={{
              background: contactUS,
              cursor: 'pointer',
              backgroundPosition: 'center center',
              backgroundSize: 'cover',
            }}
          />
            {contact_us}
          </span>
          <LogOutButton setDrawerOpen={setDrawerOpen} Exite={Exite}/>
        </Stack>
      </div>
    </React.Fragment>
  );
};

export default MainMenuDrawerOptions;
