import React from 'react';
import { Container, Drawer, Stack } from '@mui/material';
import { ArrowForwardIos } from '@mui/icons-material';
import styles from '../../Styles/Main.Menu.Drawer.module.scss';
import MenuDrawerSummary from './components/MenuDrawerSummary';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import useCheckScreenWidth from 'Global/Hooks/useCheckScreenWidth';
import DesktopSideBarAccordionsForNonProfile from '../MainTopProfileSummary/DesktopSideBarAccordionsForNonProfile';
const MainMenuDrawer = ({
  IsDrawerOpen,
  setDrawerOpen,
  setRefreshInterface,
  setRefreshForImage,
}) => {
  const screenWidth = useCheckScreenWidth(); // 1024 for Desktop

  return (
    <React.Fragment>
      <Drawer
        variant="persistent"
        anchor="right"
        open={IsDrawerOpen}
        classes={{ paper: styles.ProfileDrawer }}
        hideBackdrop
        PaperProps={{
          sx: {
            background: `${themeColors.ColorSecondary} !important`,
            color: `${themeColors.ColorPrimary} !important`,
            zIndex: 2147483647,
            borderLeft:`1px solid ${themeColors.ColorPrimary}`
          },
        }}
      >
        <span
          style={{
            backgroundColor:themeColors.ColorPrimary,
            color: `${themeColors.ColorSecondary} !important`,
            position: 'fixed',
            width: '100%',
            paddingLeft: '.5em',
            paddingTop: '.4em',
          }}
          onClick={() => setDrawerOpen(false)}
          className="main-menu-drawer"
        >
          <ArrowForwardIos
            sx={{
              cursor: 'pointer',
              fontSize: '1.6em !important',
              p: '0 !important',
              m: '0 !important',
              color: `${themeColors.ColorSecondary} !important`,
            }}
          />
        </span>

        <Container
          sx={{
            pb: '1em',
            mt: '5em',
          }}
        >
          <Stack spacing={2}>
            <Stack
              // sx={{ backgroundColor: "white" }}
              // direction="row"
              // justifyContent="space-between"
              // alignItems="center"
              padding="0"
            >
              <MenuDrawerSummary styles={styles} />
            </Stack>

            <Stack>
              <DesktopSideBarAccordionsForNonProfile
                screenWidth={screenWidth}
                setDrawerOpen={setDrawerOpen}
                setRefreshInterface={setRefreshInterface}
                setRefreshForImage={setRefreshForImage}
                isMobile={true}
              />
            </Stack>

            {/* 
            <MainMenuDrawerOptions
              setDrawerOpen={setDrawerOpen}
              setRefreshInterface={setRefreshInterface}
              setRefreshForImage={setRefreshForImage}
              screenWidth={screenWidth}
            /> */}
          </Stack>
        </Container>
      </Drawer>
    </React.Fragment>
  );
};

export default MainMenuDrawer;
