import {
  AppBar,
  Container,
  IconButton,
  Stack,
  Toolbar,
  Box,
} from '@mui/material';
import React, { useContext } from 'react';
import { Menu, Search } from '@mui/icons-material';
import DesktopSideBar from './DesktopSideBar';
import AppBarCustom from 'Global/Components/CommonComponents/AppBarCustom/AppBarCustom';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';

import MenuLinksCreator from 'Global/Components/Generators/MenuLinksCreator/MenuLinksCreator';
import OptionsDrawer from 'Global/Components/CommonComponents/BottomBar/Components/OptionsDrawer';
import { DesktopLinks } from './DesktopLinks';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';

import Cookies from 'js-cookie';
import useClock from 'Global/Hooks/useClock/useClock';

const MainTopProfileSummary = ({
  setDrawerOpen,
  screenWidth,
}) => {
  const HandleOpenDrawer = () => {
    setDrawerOpen((prev) => !prev);
  };
  const rootPaths = useContext(GlobalContext)?.rootPaths;

  const IsDrawerOpen =
    useContext(GlobalContext)?.settingDrawerOpen;

  const setSettingDrawerOpen =
    useContext(GlobalContext)?.setSettingDrawerOpen;

  const isTurnAllowed =
    useContext(GlobalContext)?.isTurnAllowed;

  const lnks = DesktopLinks(isTurnAllowed);

  // const searh_label = FindLangKey('general-public-search');

  const userCookie = Cookies.get('user');
  const userAccessRole =
    userCookie && JSON.parse(userCookie)?.AccessRole;

  // const clock = useClock(false);

  // console.log('clock=>', clock);

  // console.log('userAccessRole->', userAccessRole);

  return (
    <Container
      sx={{
        position: screenWidth > 1024 ? 'absolute' : '',
      }}
    >
      <Stack
        direction="row"
        alignItems="center"
        marginTop={screenWidth > 1024 ? '0' : '1em'}
        justifyContent="space-between"
        paddingX={{ lg: '7.8em' }}
      >
        <AppBar
          color={screenWidth>1024?'colorSecondary':'colorPrimer'}
          sx={{
            width:
              screenWidth > 1024
                ? screenWidth > 1360
                  ? '19%'
                  : '20%'
                : '100%',
            height: screenWidth > 1024 ? '100%' : 'initial',
            color:`${themeColors.ColorPrimary} !important`,
            borderLeft:`1px solid ${themeColors.ColorPrimary}`
          }}
        >
          <Toolbar
            variant="dense"
            sx={{
              minHeight: '2.3em !important',
              flexDirection:
                screenWidth > 1024
                  ? 'column'
                  : 'row-reverse',
              height:
                screenWidth > 1024 ? '100%' : 'initial',
              paddingInline:
                screenWidth > 1024
                  ? '0 !important'
                  : 'initial',
              justifyContent: 'space-between',
               color:`${themeColors.ColorPrimary} !important`
            }}
          >
            {screenWidth < 1024 ? (
              <>
                <IconButton
                  sx={{ color: themeColors.ColorSecondary }}
                  aria-label="menu"
                  onClick={HandleOpenDrawer}
                >
                  <Menu
                    sx={{
                      mr: '.39em',
                      cursor: 'pointer',
                      fill: `${screenWidth>1024?themeColors.ColorPrimary:themeColors.ColorSecondary} !important`,
                    }}
                  />
                </IconButton>
              </>
            ) : (
              <Stack>
                <DesktopSideBar />

                {/* <AppBarCustom
                  styles={{
                    width: '7em',
                    backgroundColor: 'white',
                    marginRight: '15%',
                    '& *': {
                      color: 'black !important',
                    },
                    overflowY: 'auto',
                  }}
                  screenWidth={screenWidth}
                >
                  <MenuLinksCreator
                    Links={lnks}
                    AccessRoles={rootPaths}
                    isTurnAllowed={isTurnAllowed}
                    screenWidth={screenWidth}
                  />
                </AppBarCustom> */}

                <OptionsDrawer
                  IsDrawerOpen={IsDrawerOpen}
                  setDrawerOpen={setSettingDrawerOpen}
                />
              </Stack>
            )}
            {/* {screenWidth > 1024 ? <></> : <p>{clock}</p>} */}
            <>
              {screenWidth < 1024 ? (
                <IconButton
                  sx={{
                    color: themeColors.ColorSecondary,
                  }}
                  aria-label="menu"
                  onClick={HandleOpenDrawer}
                >
                  <Search
                    sx={{
                      mr: '.39em',
                      cursor: 'pointer',
                      fill: `${themeColors.ColorSecondary} !important`,
                    }}
                  />
                </IconButton>
              ) : (
                <></>
              )}
            </>
          </Toolbar>
        </AppBar>
      </Stack>
    </Container>
  );
};

export default MainTopProfileSummary;

