import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import Cookies from 'js-cookie';
import React, { useContext } from 'react';

import { DesktopLinks } from '../Components/MainTopProfileSummary/DesktopLinks';
import { Links as MobileLinks } from '../Components/MainMenu/variables/Links';

const useHandleSIdeBarAccordion_Bl = (
  isMobile,
  screenWidth
) => {
  const myProfile_label = FindLangKey(
    'main-menu-desktop-sidebar-profile-accordion'
  );

  const patient_label = FindLangKey(
    'general-public-label-doctor-role'
  );

  const doctor_label = FindLangKey(
    'general-public-label-patient-role'
  );

  const IdPerson =
    Cookies.get('user') &&
    JSON.parse(Cookies.get('user'))?.IdPerson;
  const isTurnAllowed =
    useContext(GlobalContext)?.isTurnAllowed;
  const Links = isMobile
    ? MobileLinks(screenWidth, false, IdPerson)
    : DesktopLinks(isTurnAllowed, IdPerson);
  const AccessRoles = useContext(GlobalContext)?.rootPaths;

  const doctorItems =
    Links &&
    Links?.filter(
      (option, i) =>
        (AccessRoles?.map((role) =>
          role.toLowerCase()
        )?.includes(option.Route.toLowerCase()) ||
          AccessRoles?.map((role) =>
            role.toLowerCase().substring(1)
          )?.includes(option.Route.toLowerCase())) &&
        !option?.IdPatient
    );
  const patientItems =
    Links &&
    Links?.filter(
      (option, i) =>
        (AccessRoles?.map((role) =>
          role.toLowerCase()
        )?.includes(option.Route.toLowerCase()) ||
          AccessRoles?.map((role) =>
            role.toLowerCase().substring(1)
          )?.includes(option.Route.toLowerCase())) &&
        option?.IdPatient
    );

 
  return {
    myProfile_label,
    patient_label,
    doctor_label,
    IdPerson,
    isTurnAllowed,
    Links,
    AccessRoles,
    doctorItems,
    patientItems
  };
};

export default useHandleSIdeBarAccordion_Bl;
