import Dexie from 'dexie';
import React, { useEffect, useState } from 'react';

let db = new Dexie('carouselDb');
db.version(1).stores({
  images: '++id, url',
});

const useCacheCarouselImages = (newImages) => {
  const [cachedImages, setCachedImages] = useState(null);

  async function addImages(images) {
    const existingImages = await db.images.toArray();

    const newImagesToAdd = images.filter((image) => {
      return !existingImages.some(
        (existingImage) => existingImage.url === image.url
      );
    });

    if (newImagesToAdd.length > 0) {
      await db.images.bulkAdd(newImagesToAdd);
      console.log(
        `Added ${newImagesToAdd.length} new images to the database.`
      );
    } else {
      // console.log('No new images to add.');
    }
  }

  async function getImagesArray(retryCount = 0) {
    try {
      const images = await db.images.toArray();
      return images;
    } catch (err) {
      if (err.name === 'DatabaseClosedError' && retryCount < 3) {
        console.log('Database closed. Attempting to reopen...');
        db = new Dexie('carouselDb');
        db.version(1).stores({
          images: '++id, url',
        });
        await db.open();
        return getImagesArray(retryCount + 1);
      } else {
        console.error(
          'Error retrieving images from database:',
          err
        );
        return [];
      }
    }
  }

  useEffect(() => {
    const fetchAndCacheImages = async () => {
      const cachedImagesArray = await getImagesArray();
      if (cachedImagesArray.length > 0) {
        localStorage.setItem(
          'carouselCached',
          JSON.stringify(true)
        );
        return;
      }
      localStorage.setItem(
        'carouselCached',
        JSON.stringify(false)
      );
    };

    fetchAndCacheImages();
  }, []);

  useEffect(() => {
    const fetchAndCacheImages = async () => {
      const cachedImagesArray = await getImagesArray();

      if (cachedImagesArray.length > 0) {
        localStorage.setItem(
          'carouselCached',
          JSON.stringify(true)
        );
        setCachedImages({
          List: cachedImagesArray,
        });
      } else if (newImages) {
        localStorage.setItem(
          'carouselCached',
          JSON.stringify(true)
        );
        await addImages(newImages);
        const updatedCachedImagesArray =
          await getImagesArray();
        setCachedImages({
          List: updatedCachedImagesArray,
        });
      }
    };

    fetchAndCacheImages();
  }, [newImages]);

  return cachedImages;
};

export default useCacheCarouselImages;