import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import { IconAtom } from 'Global/Hooks/useCacheIcons/IconAtom';
import { useAtom } from 'jotai';
import Cookies from 'js-cookie';

export const DesktopLinks = (isTurnAllowed) => {
  const [icons] = useAtom(IconAtom);
  const cachedIcons =
    icons || JSON.parse(localStorage.getItem('icons'));

  const IdPatient = Cookies.get('user')
    ? JSON.parse(Cookies.get('user')).IdPatient
    : '';

  const currentPatientTitle = FindLangKey(
    'main-menu-button-current-patient'
  );
  const patientsListTitle = FindLangKey(
    'general-public-patients-list'
  );
  const newReferralTitle = FindLangKey(
    'general-public-new-refferal'
  );
  const performanceReportTitle = FindLangKey(
    'general-public-performance-report'
  );
  const uploadFileTitle = FindLangKey(
    'main-menu-button-upload-file'
  );
  const wageTitle = FindLangKey('main-menu-button-wage');
  const getAppointmentTitle = FindLangKey(
    'main-menu-button-get-appointment'
  );
  const appointmentsTitle = FindLangKey(
    'main-menu-button-appointments'
  );
  const labTestResultTitle = FindLangKey(
    'lab-test-result-navbar-title-lab'
  );
  const freezeContractTitle = FindLangKey(
    'main-menu-drawer-link-freeze-contract'
  );
  const invoiceReceiptTitle = FindLangKey(
    'main-menu-button-invoice-receipt'
  );
  const quarteringTitle = FindLangKey(
    'page-title-quartering'
  );
  const labActionsTitle = FindLangKey(
    'main-menu-button-lab-actions'
  );
  const patologoyLabTitle = FindLangKey(
    'patologoy-lab-route-title'
  );
  const fileArchive_label = FindLangKey(
    'genral-public-btn-label-download-file'
  );
  const SendfileArchive_label = FindLangKey(
    'main-menu-button-patient-send-file'
  );
  const pacsImages = FindLangKey(
    'main-menu-button-pacs-images'
  );

  const PatientSurveyList_title = FindLangKey(
    'user-survey-route-title'
  );

  const PatientCartable_label = FindLangKey(
    'patient-cartable-navbar-title'
  );

  const patientForVisit_label = FindLangKey(
    'patients-for-visit-navbar-title'
  );
  const savePatientsForVisit_label = FindLangKey(
    'save-patients-for-visit-navbar-title'
  );
  const submitErjaTitle = FindLangKey(
    'submit-erja-title'
  );

  const data = [
    // svg file names are equivalent to route names

    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['NewTurnLine.svg'],
          Route: 'GetNewTurn',
          Title: getAppointmentTitle,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['Turns.svg'],
          Route: 'TurnList',
          Title: appointmentsTitle,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['Seeanswer.svg'],
          Route: 'LabTestResult',
          Title: labTestResultTitle,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['FreezeContract.svg'],
          Route: 'FreezeContract',
          Title: freezeContractTitle,
          IdPatient,
        },

    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['DownloadFile.svg'],
          Route: 'FileArchive',
          Title: fileArchive_label,
          IdPatient,
        },

    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['Medicalimages.svg'],
          Route: 'pacsImages',
          Title: pacsImages,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['UploadFile.svg'],
          Route: 'SendFileArchive',

          Title: SendfileArchive_label,
          IdPatient,
        },

    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['CurrentPatient.svg'],

      Route: 'CurrentPatients',
      Title: currentPatientTitle,
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['PatientList.svg'],
      Route: 'PatientList',
      Title: patientsListTitle,
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['PatientReferral.svg'],
      Route: 'SearchPatientForErja',
      Title: newReferralTitle,
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['PerformanceReport.svg'],
      Route: 'ServiceReportSummary',
      Title: performanceReportTitle, //title is the text goes on top bar of each section
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['Upload.svg'],
      Route: '',
      Title: uploadFileTitle,
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['Wage.svg'],
      Route: '',
      Title: wageTitle,
    },

    // {
    //   Picture:
    //     'data:image/svg+xml;base64,' +
    //     cachedIcons?.MenueIcon['InvoiceReceipt.svg'],
    //   Route: 'InvoiceReceipt',
    //   Title: invoiceReceiptTitle,
    // },
    // {
    //   Picture:
    //     'data:image/svg+xml;base64,' +
    //     cachedIcons?.MenueIcon['CheckInout.svg'],
    //   Route: 'Quartering',
    //   Title: quarteringTitle,
    // },

    // {
    //   Picture:
    //     'data:image/svg+xml;base64,' +
    //     cachedIcons?.MenueIcon['AnswerTestLab.svg'],
    //   Route: 'Lab',
    //   Title: labActionsTitle,
    // },
    // {
    //   Picture:
    //     'data:image/svg+xml;base64,' +
    //     cachedIcons?.MenueIcon['Specializedlaboratory.svg'],
    //   Route: 'Pato',
    //   Title: patologoyLabTitle,
    // },

    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['Poll.svg'],
          Route: 'PatientSurveyList',
          Title: PatientSurveyList_title,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['workReport.svg'],
          Route: 'Cartable',
          Title: PatientCartable_label,
          IdPatient,
        },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['PatientList.svg'],
      Route: 'PatientsForVisit',
      Title: patientForVisit_label,
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['Reception.svg'],
      Route: 'SavePatientsForVisit',

      Title: submitErjaTitle,
    },
 
  ];
  return data.filter((fl) => fl !== null);
};
