import MenuLinksCreator from 'Global/Components/Generators/MenuLinksCreator/MenuLinksCreator'
import React from 'react'

export default function GridItems({userAccessRole,allLinks,screenWidth,rootPaths,isTurnAllowed,lnks}) {
  return (
   <>
    {userAccessRole &&
          userAccessRole.includes('Patient') ? (
            <>
              {allLinks.filter(
                (fl) =>
                  rootPaths?.includes(fl.Route) ||
                  lnks
                    ?.map((link) => link.Route)
                    ?.includes(fl.Route)
              ).length > 0 ? (
                userAccessRole?.includes('Patient') ? (
                  <MenuLinksCreator
                    Links={
                      screenWidth > 1024 ? lnks : allLinks
                    }
                    AccessRoles={rootPaths}
                    isTurnAllowed={isTurnAllowed}
                    screenWidth={screenWidth}
                  />
                ) : (
                  <></>
                )
              ) : (
                <></>
              )}
            </>
          ) : (
            userAccessRole && (
              <MenuLinksCreator
                Links={screenWidth > 1024 ? lnks : allLinks}
                AccessRoles={rootPaths}
                isTurnAllowed={isTurnAllowed}
                screenWidth={screenWidth}
              />
            )
          )}
   </>
  )
}
