import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import { useContext } from 'react';
import { UploadUserImage } from 'Routes/Protected/MainPage/Components/MainMenuDrawer/Functions/UploadUserImage';

export const fileSelector = (
  format,
  setRefreshInterface,
  GlobalConstants,
  operationDoneMsg,
  setRefreshForImage,
  operatingMsg,
  exceededLabel
) => {
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    const maxSize = 400 * 1024; // 400KB in bytes

    if (file.size > maxSize) {
      SwalAlert('error', exceededLabel, false);
      return;
    }

    if (file.type.includes(`${format}/`)) {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        UploadUserImage(
          reader.result,
          setRefreshInterface,
          GlobalConstants,
          operationDoneMsg,
          setRefreshForImage,
          operatingMsg
        );
      };
      reader.onerror = () => {
        console.log('file Error');
      };
    } else {
      SwalAlert(
        'error',
        '    ',
        false
      );
    }
  };
  return handleFileChange;
};
