import React from 'react';
import { Stack } from '@mui/material';
import ImageTitleStack from './ImageTitleStack';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';

const ImageTitlePack = ({ userName }) => {
  const cachedIcons = JSON.parse(
    localStorage.getItem('icons')
  );
  const svgHeader = 'data:image/svg+xml;base64,';
  return (
    <React.Fragment>
      <Stack alignItems="center" spacing={1} mt={1}>
        {/* <ImageTitleStack
          source={FullName}
          alt={"   "}
          paragraph={userName?.FullName}
        /> */}
        {/* <ImageTitleStack
          source={NationalCode}
          alt={""}
          paragraph={userName?.NationalCode}
        /> */}
        <ImageTitleStack
          source={
            svgHeader +
            cachedIcons?.GeneralIcon['FullName.svg']
          }
          alt={''}
          paragraph={userName?.AdditionalInfo?.Speciality}
          accessroleneeded={
            userName.AccessRole &&
            userName?.AccessRole?.includes('Patient')
          }
        />
        <p
          style={{
            color: `${themeColors.ColorPrimary}`,
          }}
        >
          {userName?.NationalCode}
        </p>
        {/* 
        <ImageTitleStack
          source={svgHeader + cachedIcons?.GeneralIcon["Score.svg"]}
          alt={""}
          paragraph={userName?.AdditionalInfo?.Score}
          isScore={true}
        /> */}
      </Stack>
    </React.Fragment>
  );
};

export default ImageTitlePack;
