import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import { IconAtom } from 'Global/Hooks/useCacheIcons/IconAtom';
import { useAtom } from 'jotai';
import Cookies from 'js-cookie';

export const Links = (screenWidth, isTurnAllowed) => {
  const [icons] = useAtom(IconAtom);
  const cachedIcons =
    icons || JSON.parse(localStorage.getItem('icons'));

  // console.log('cachedIconscachedIcons--->', cachedIcons);

  const IdPatient = Cookies.get('user')
    ? JSON.parse(Cookies.get('user')).IdPatient
    : '';

  const get_appointment = FindLangKey(
    'main-menu-button-get-appointment'
  );
  const appointments = FindLangKey(
    'main-menu-button-appointments'
  );
  const lab_test_result = FindLangKey(
    'lab-test-result-navbar-title-lab'
  );

  const FreezeContract = FindLangKey(
    'main-menu-drawer-link-freeze-contract'
  );
  const fileArchive_label = FindLangKey(
    'genral-public-btn-label-download-file'
  );
  const SendfileArchive_label = FindLangKey(
    'main-menu-button-patient-send-file'
  );

  const pacsImages = FindLangKey(
    'main-menu-button-pacs-images'
  );

  const CurrentPatient_label = FindLangKey(
    'main-menu-button-current-patient'
  );

  const patientList_label = FindLangKey(
    'general-public-patients-list'
  );

  const newReferral_label = FindLangKey(
    'general-public-new-refferal'
  );

  const serviceReport_label = FindLangKey(
    'general-public-performance-report'
  );

  const uploadFile_label = FindLangKey(
    'main-menu-button-upload-file'
  );

  const wages_label = FindLangKey('main-menu-button-wage');

  const invoiceReceipt_label = FindLangKey(
    'main-menu-button-invoice-receipt'
  );

  const quartering_label = FindLangKey(
    'page-title-quartering'
  );

  const lab_actions = FindLangKey(
    'main-menu-button-lab-actions'
  );

  const pato_title = FindLangKey(
    'patologoy-lab-route-title'
  );
  const PatientSurveyList_title = FindLangKey(
    'user-survey-route-title'
  );

  const PatientCartable_label = FindLangKey(
    'patient-cartable-navbar-title'
  );
  const patientForVisit_label = FindLangKey(
    'patients-for-visit-navbar-title'
  );

  const savePatientsForVisit_label = FindLangKey(
    'save-patients-for-visit-navbar-title'
  );

  const userCookie = Cookies.get('user');

  const userAccessRole =
    userCookie && JSON.parse(userCookie)?.AccessRole;

  const isOnlyPatient =
    userAccessRole?.includes('Patient') &&
    userAccessRole?.length === 1 &&
    screenWidth > 1024;

  const links = [
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['NewTurnLine.svg'],
          Route: isOnlyPatient
            ? 'GetNewTurn'
            : '/GetNewTurn',
          Title: get_appointment,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['Turns.svg'],
          Route: isOnlyPatient ? 'TurnList' : '/TurnList',
          Title: appointments,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['Seeanswer.svg'],
          Route: isOnlyPatient
            ? 'LabTestResult'
            : '/LabTestResult',
          Title: lab_test_result,
          IdPatient,
        },

    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['FreezeContract.svg'],
          Route: isOnlyPatient
            ? 'FreezeContract'
            : '/FreezeContract',
          Title: FreezeContract,
          IdPatient,
        },

    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['Medicalimages.svg'],
          Route: isOnlyPatient
            ? 'pacsImages'
            : '/pacsImages',
          Title: pacsImages,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['DownloadFile.svg'],
          Route: isOnlyPatient
            ? 'FileArchive'
            : '/FileArchive',
          Title: fileArchive_label,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['SendFile.svg'],
          Route: isOnlyPatient
            ? 'SendFileArchive'
            : '/SendFileArchive',
          Title: SendfileArchive_label,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['Poll.svg'],
          Route: isOnlyPatient
            ? 'PatientSurveyList'
            : '/PatientSurveyList',
          Title: PatientSurveyList_title,
          IdPatient,
        },
    IdPatient === 0
      ? null
      : {
          Picture:
            'data:image/svg+xml;base64,' +
            cachedIcons?.MenueIcon['workReport.svg'],
          Route: isOnlyPatient ? 'Cartable' : '/Cartable',
          Title: PatientCartable_label,
          IdPatient,
        },

    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['CurrentPatient.svg'],

      Route: isOnlyPatient
        ? 'CurrentPatients'
        : '/CurrentPatients',
      Title: CurrentPatient_label,
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['PatientList.svg'],
      Route: isOnlyPatient ? 'PatientList' : '/PatientList',
      Title: patientList_label,
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['PatientReferral.svg'],
      Route: isOnlyPatient
        ? 'SearchPatientForErja'
        : '/SearchPatientForErja',
      Title: newReferral_label,
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['PerformanceReport.svg'],
      Route: isOnlyPatient
        ? 'ServiceReportSummary'
        : '/ServiceReportSummary',
      Title: serviceReport_label, //title is the text goes on top bar of each section
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['Upload.svg'],
      Route: '',
      Title: uploadFile_label,
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['Wage.svg'],
      Route: '',
      Title: wages_label,
    },

    // {
    //   Picture:
    //     'data:image/svg+xml;base64,' +
    //     cachedIcons?.MenueIcon['InvoiceReceipt.svg'],
    //   Route: isOnlyPatient
    //     ? 'InvoiceReceipt'
    //     : '/InvoiceReceipt',
    //   Title: invoiceReceipt_label,
    // },
    // {
    //   Picture:
    //     'data:image/svg+xml;base64,' +
    //     cachedIcons?.MenueIcon['CheckInout.svg'],
    //   Route: isOnlyPatient ? 'Quartering' : '/Quartering',
    //   Title: quartering_label,
    // },

    // {
    //   Picture:
    //     'data:image/svg+xml;base64,' +
    //     cachedIcons?.MenueIcon['AnswerTestLab.svg'],
    //   Route: isOnlyPatient ? 'Lab' : '/Lab',
    //   Title: lab_actions,
    // },
    // {
    //   Picture:
    //     'data:image/svg+xml;base64,' +
    //     cachedIcons?.MenueIcon['Specializedlaboratory.svg'],
    //   Route: isOnlyPatient ? 'Pato' : '/Pato',
    //   Title: pato_title,
    // },

    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['PatientList.svg'],
      Route: isOnlyPatient
        ? 'PatientsForVisit'
        : '/PatientsForVisit',
      Title: patientForVisit_label,
    },
    {
      Picture:
        'data:image/svg+xml;base64,' +
        cachedIcons?.MenueIcon['Reception.svg'],
      Route: isOnlyPatient
        ? 'SavePatientsForVisit'
        : '/SavePatientsForVisit',
      Title: savePatientsForVisit_label,
    },
  ];

  const filteredLinks = links.filter((fl) => fl !== null);

  return filteredLinks;
};
