import { ArrowDownward } from '@mui/icons-material';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Typography,
} from '@mui/material';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import React from 'react';

const SideBarAccordion = ({
  children,
  expandedAccordion,
  handleAccordionChange,
  pannelNo = '',
  label = '',
}) => {
  return (
    <React.Fragment>
      <Accordion
        expanded={expandedAccordion === pannelNo}
        onChange={handleAccordionChange(pannelNo)}
      >
        <AccordionSummary
          sx={{
            direction: 'rtl',
          }}
          expandIcon={
            <ArrowDownward
              sx={{
                fill: `${themeColors.ColorPrimary} !important`,
              }}
              fontSize="small"
            />
          }
          aria-controls={`${pannelNo}-content`}
          id={`${pannelNo}-header`}
        >
          <Typography
            sx={{
              color: `${themeColors.ColorPrimary} !important`,
            }}
          >
            {label}
          </Typography>
        </AccordionSummary>
        <AccordionDetails
          sx={{
            maxHeight: 'fit-content',
          }}
        >
          {children}
        </AccordionDetails>
      </Accordion>
    </React.Fragment>
  );
};

export default SideBarAccordion;
