import React, { useEffect, useState } from 'react';
import UserInterface from './UserInterface';
import { useAtom } from 'jotai';
import { ImageAtom } from './ImageAtom';

import { useLocation, useNavigate } from 'react-router-dom';
import useCheckScreenWidth from 'Global/Hooks/useCheckScreenWidth';
import ComponentMemoizer from 'Global/Components/ComponentMemoizer/ComponentMemoizer';
const MainPage = () => {
  const [forceRender, setForceRender] = useState();

  const [RefreshForImage, setRefreshForImage] =
    useAtom(ImageAtom);


  

  useEffect(() => {
    setForceRender(
      JSON.parse(localStorage.getItem('languageArray'))
    );
  }, []);

  return (
    <React.Fragment>
      <ComponentMemoizer
        element={
          <UserInterface
            setRefreshForImage={setRefreshForImage}
          />
        }
        dependecies={[
          setRefreshForImage,
          RefreshForImage,
          forceRender,
        ]}
      />
    </React.Fragment>
  );
};

export default MainPage;
