import React, { useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import styles from '../../../Styles/Menu.Options.module.scss';
import ComponentMemoizer from 'Global/Components/ComponentMemoizer/ComponentMemoizer';
import { sideBarLinks } from 'Routes/Protected/MainPage/shared/side.bar.links';
import { useAtom } from 'jotai';
import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import PatientItemsMobile from './PatientItemsMobile';
import PatientItemsDesktop from './PatientItemsDesktop';

const PatientItems = (props) => {
  const {
    screenWidth,
    Links,
    AccessRoles,
    isTurnAllowed,
    isNestedRoute,
  } = props;

  // let lastIndex = -1;
  // for (let i = Links.length - 1; i >= 0; i--) {
  //   if (Links[i].hasOwnProperty('IdPatient')) {
  //     lastIndex = i;
  //     break;
  //   }
  // }

  return (
    <React.Fragment>
      {screenWidth > 1024 ? (
        <PatientItemsDesktop
          Links={Links}
          AccessRoles={AccessRoles}
          isTurnAllowed={isTurnAllowed}
          isNestedRoute={isNestedRoute}
        />
      ) : (
        <PatientItemsMobile
          Links={Links}
          AccessRoles={AccessRoles}
          isTurnAllowed={isTurnAllowed}
          isNestedRoute={isNestedRoute}
          screenWidth={screenWidth}
        />
      )}
    </React.Fragment>
  );
};

export default PatientItems;
