import React, { useEffect } from 'react';
import {
  Link,
  useLocation,
  useNavigate,
} from 'react-router-dom';
import styles from '../../../Styles/Menu.Options.module.scss';
import ComponentMemoizer from 'Global/Components/ComponentMemoizer/ComponentMemoizer';
import { sideBarLinks } from 'Routes/Protected/MainPage/shared/side.bar.links';
import { useAtom } from 'jotai';
import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';

const DoctorItemsMobile = (props) => {
  const {
    screenWidth,
    Links,
    AccessRoles,
    isTurnAllowed,
    isNestedRoute,
  } = props;

  const navigate = useNavigate();

  const navigateTo = (route) => {
    navigate(route);
  };

  return (
    <React.Fragment>
      {screenWidth > 1024 ? (
        <div
          style={{
            flexShrink: '1',
             
          }}
        >
          <ComponentMemoizer
            element={Links?.map(
              (option, i) =>
                (AccessRoles?.map((role) =>
                  role.toLowerCase()
                )?.includes(option.Route.toLowerCase()) ||
                  AccessRoles?.map((role) =>
                    role.toLowerCase().substring(1)
                  )?.includes(
                    option.Route.toLowerCase()
                  )) &&
                !option?.IdPatient && (
                  <React.Fragment key={i}>
                    <section
                      className={styles.eachLink}
                      onClick={() => {
                        if (
                          [
                            '/TurnList',
                            '/GetNewTurn',
                          ].includes(option.Route) &&
                          !isTurnAllowed
                        ) {
                          SwalAlert(
                            'warning',
                            cant_get_newTurn_label,
                            false,
                            false,
                            false,
                            false,
                            false,
                            true
                          );
                        } else {
                          navigateTo(option.Route);
                        }
                      }}
                    >
                      <img
                        src={option.Picture}
                        alt={option.Title}
                        loading="..."
                        style={{
                          background: option.Picture,
                          cursor: 'pointer',
                          backgroundPosition:
                            'center center',
                          backgroundSize: 'cover',
                          filter: [
                            '/TurnList',
                            '/GetNewTurn',
                          ].includes(option.Route)
                            ? !isTurnAllowed
                              ? 'grayscale(1)'
                              : 'unset'
                            : 'unset',
                        }}
                        width={
                          screenWidth >= 1440 &&
                          isNestedRoute
                            ? 60
                            : !isNestedRoute
                            ? 60
                            : 40
                        }
                        height={
                          screenWidth >= 1440 &&
                          isNestedRoute
                            ? 60
                            : !isNestedRoute
                            ? 60
                            : 40
                        }
                      />

                      <p style={{ fontSize: '0.8125em' }}>
                        {option.Title}
                      </p>
                    </section>
                  </React.Fragment>
                )
            )}
            dependecies={[
              localStorage.getItem('icons'),
              isTurnAllowed,
            ]}
          />
        </div>
      ) : (
        <ComponentMemoizer
          element={Links?.map(
            (option, i) =>
              (AccessRoles?.map((role) =>
                role.toLowerCase()
              )?.includes(option.Route.toLowerCase()) ||
                AccessRoles?.map((role) =>
                  role.toLowerCase().substring(1)
                )?.includes(option.Route.toLowerCase())) &&
              !option?.IdPatient && (
                <React.Fragment key={i}>
                  <section
                    className={styles.eachLink}
                    onClick={() => {
                      if (
                        [
                          '/TurnList',
                          '/GetNewTurn',
                        ].includes(option.Route) &&
                        !isTurnAllowed
                      ) {
                        SwalAlert(
                          'warning',
                          cant_get_newTurn_label,
                          false,
                          false,
                          false,
                          false,
                          false,
                          true
                        );
                      } else {
                        navigateTo(option.Route);
                      }
                    }}
                  >
                    <img
                      src={option.Picture}
                      alt={option.Title}
                      loading="..."
                      style={{
                        background: option.Picture,
                        cursor: 'pointer',
                        backgroundPosition: 'center center',
                        backgroundSize: 'cover',
                        filter: [
                          '/TurnList',
                          '/GetNewTurn',
                        ].includes(option.Route)
                          ? !isTurnAllowed
                            ? 'grayscale(1)'
                            : 'unset'
                          : 'unset',
                      }}
                      width={
                        screenWidth >= 1440 && isNestedRoute
                          ? 60
                          : !isNestedRoute
                          ? 60
                          : 40
                      }
                      height={
                        screenWidth >= 1440 && isNestedRoute
                          ? 60
                          : !isNestedRoute
                          ? 60
                          : 40
                      }
                    />

                    <p style={{ fontSize: '0.8125em' }}>
                      {option.Title}
                    </p>
                  </section>
                </React.Fragment>
              )
          )}
          dependecies={[
            localStorage.getItem('icons'),
            isTurnAllowed,
          ]}
        />
      )}
    </React.Fragment>
  );
};

export default DoctorItemsMobile;
