import { Info } from '@mui/icons-material';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import React from 'react';
import { useNavigate } from 'react-router-dom';

const PatientDoctor = ({
  screenWidth,
  route,
  image = '',
  title = '',
}) => {
  const navigate = useNavigate();

  // Function to get the dynamic icon component
  const getDynamicIcon = (iconName) => {
    const IconComponent = require('@mui/icons-material')[
      iconName
    ];
    return IconComponent ? (
      <IconComponent
        color="colorPrimer"
        sx={{
          fontSize: '1.5em !important',
        }}
      />
    ) : null;
  };

  return (
    <React.Fragment>
      <div
        style={{
          width: '100%',
          color: `${themeColors.ColorPrimary} !important`,
          overflowY: screenWidth < 1400 ? 'visible' : '',
          maxHeight: screenWidth < 1400 ? '20em' : '',
        }}
      >
        <span
          onClick={() => navigate(`${route}`)}
          style={{
            display: 'flex',
            flexDirection: 'row-reverse',
            alignItems: 'center',
            gap: '.5em',
            cursor: 'pointer',
          }}
        >
          <img
            src={image}
            alt={title}
            width="25em"
            height="25em"
            style={{
              background: image,
              cursor: 'pointer',
              backgroundPosition: 'center center',
              backgroundSize: 'cover',
            }}
          />
          <p>{title}</p>
        </span>
      </div>
    </React.Fragment>
  );
};

export default PatientDoctor;
