import React, { useEffect } from 'react';
import {
  Link,
  useLocation,
  useNavigate,
} from 'react-router-dom';
import styles from '../../../Styles/Menu.Options.module.scss';
import ComponentMemoizer from 'Global/Components/ComponentMemoizer/ComponentMemoizer';
import { sideBarLinks } from 'Routes/Protected/MainPage/shared/side.bar.links';
import { useAtom } from 'jotai';
import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import FrameCard from 'Global/Components/CommonComponents/FrameCard/FrameCard';
const PatientItemsMobile = (props) => {
  const {
    screenWidth,
    Links,
    AccessRoles,
    isTurnAllowed,
    isNestedRoute,
  } = props;

  const navigate = useNavigate();

  const isAccessible = (route) => {
    const lowerCasedRoles = AccessRoles?.map((role) =>
      role.toLowerCase()
    );
    return (
      lowerCasedRoles?.includes(route.toLowerCase()) ||
      lowerCasedRoles
        ?.map((role) => role.substring(1))
        ?.includes(route.toLowerCase())
    );
  };

  const filteredLinks = Links?.filter(
    (option) =>
      isAccessible(option.Route) && option?.IdPatient
  );

  // console.log(
  //   'filteredLinksfilteredLinks--->',
  //   filteredLinks
  // );

  const renderLinkSection = (options) => {

 
    return options.map((option, i) => (
      option?.IdPatient &&
      <div
        key={i}
        onClick={() => {
          if (
            ['/TurnList', '/GetNewTurn'].includes(
              option.Route
            ) &&
            !isTurnAllowed
          ) {
            SwalAlert(
              'warning',
              cant_get_newTurn_label,
              false,
              false,
              false,
              false,
              false,
              true
            );
          } else {
            navigate(getLinkRoute(option));
          }
        }}
        style={{
          zIndex: 2,
        }}
      >
        <div className={styles.eachLink}>
          <img
            src={option.Picture}
            alt={option.Title}
            loading="..."
            className={getImageStyle(option)}
            width={getImageSize(
              screenWidth,
              isNestedRoute,
              true
            )}
            height={getImageSize(
              screenWidth,
              isNestedRoute,
              false
            )}
          />

          <p style={{ fontSize: '0.8125em' }}>
            {option.Title}
          </p>
        </div>
      </div>
    ));
  };

  const getLinkRoute = (option) => {
    if (
      ['/TurnList', '/GetNewTurn'].includes(option.Route)
    ) {
      return !isTurnAllowed ? '' : option.Route;
    }
    return option.Route;
  };

  const getImageStyle = (option) => {
    const style = {
      background: option.Picture,
      cursor: 'pointer',
      backgroundPosition: 'center center',
      backgroundSize: 'cover',
    };

    if (
      ['/TurnList', '/GetNewTurn'].includes(option.Route)
    ) {
      style.filter = !isTurnAllowed
        ? 'grayscale(1)'
        : 'unset';
    }

    return style;
  };

  const getImageSize = (
    screenWidth,
    isNestedRoute,
    isWidth
  ) => {
    if (screenWidth >= 1440 && isNestedRoute) {
      return isWidth ? 60 : 60;
    }
    return !isNestedRoute ? 60 : 40;
  };

  const filterLinksByAccessRoles = () => {
    return Links?.filter(
      (option) =>
        AccessRoles?.map((role) =>
          role.toLowerCase()
        )?.includes(option.Route.toLowerCase()) ||
        AccessRoles?.map((role) =>
          role.toLowerCase().substring(1)
        )?.includes(option.Route.toLowerCase())
    );
  };

  return (
    <>
      {screenWidth > 1024 ? (
        <div style={{ flexShrink: '1' }}>
          <ComponentMemoizer
            element={renderLinkSection(filteredLinks)}
            dependecies={[
              localStorage.getItem('icons'),
              isTurnAllowed,
            ]}
          />
        </div>
      ) : (
        <ComponentMemoizer
          element={renderLinkSection(
            filterLinksByAccessRoles()
          )}
          dependecies={[
            localStorage.getItem('icons'),
            isTurnAllowed,
          ]}
        />
      )}
    </>
  );
};

export default PatientItemsMobile;
