import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import { useAtom } from 'jotai';
import Cookies from 'js-cookie';
import React, { useContext, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { sideBarLinks } from 'Routes/Protected/MainPage/shared/side.bar.links';
import { IconAtom } from './useCacheIcons/IconAtom';

const useLinkCreatorBl = (
  screenWidth,
  Links,
  AccessRoles,
  isTurnAllowed
) => {
  const quartering_label = FindLangKey(
    'page-title-quartering'
  );

  const invoiceReceipt_label = FindLangKey(
    'main-menu-button-invoice-receipt'
  );

  const lab_actions = FindLangKey(
    'main-menu-button-lab-actions'
  );

  const pato_title = FindLangKey(
    'patologoy-lab-route-title'
  );

  const GlobalConstants = useContext(GlobalContext);
  const userCookie = Cookies.get('user');

  const userAccessRole =
    userCookie && JSON.parse(userCookie)?.AccessRole;

  const responsiveGirds = (theme) => ({
    gridTemplateColumns:
      GlobalConstants.MoreColumns
        ? screenWidth > 1024
          ? 'repeat(4, 1fr) !important'
          : 'repeat(4, 1fr) !important'
        : screenWidth > 1024
        ? 'repeat(3, 10em) !important'
        : 'repeat(3, 1fr) !important',
    justifyContent: screenWidth > 1024 ? 'center' : '',
    alignItems: screenWidth > 1024 ? 'center' : '',
  });

  const { pathname } = useLocation();

  const pathParts = pathname.split('/');
  const isNestedRoute =
    pathParts[1] === 'main' && pathParts.length > 2;

  const [test, setSideBarLinks] = useAtom(sideBarLinks);

  const cant_get_newTurn_label = FindLangKey(
    'get-new-turn-turn-label-cant-get-new-turn'
  );

  const [icons] = useAtom(IconAtom);
  const cachedIcons =
    icons || JSON.parse(localStorage.getItem('icons'));

  const isOnlyPatient =
    userAccessRole?.includes('Patient') &&
    userAccessRole?.length === 1 &&
    screenWidth > 1024;


 
  const personelWage_links =
    screenWidth > 1024
      ? [
          {
            Picture:
              'data:image/svg+xml;base64,' +
              cachedIcons?.MenueIcon['InvoiceReceipt.svg'],
            Route: 'InvoiceReceipt',
            Title: invoiceReceipt_label,
          },
          {
            Picture:
              'data:image/svg+xml;base64,' +
              cachedIcons?.MenueIcon['CheckInout.svg'],
            Route: 'Quartering',
            Title: quartering_label,
          },
        ]
      : [
          {
            Picture:
              'data:image/svg+xml;base64,' +
              cachedIcons?.MenueIcon['InvoiceReceipt.svg'],
            Route: isOnlyPatient
              ? 'InvoiceReceipt'
              : '/InvoiceReceipt',
            Title: invoiceReceipt_label,
          },
          {
            Picture:
              'data:image/svg+xml;base64,' +
              cachedIcons?.MenueIcon['CheckInout.svg'],
            Route: isOnlyPatient
              ? 'Quartering'
              : '/Quartering',
            Title: quartering_label,
          },
        ];

  const labs_link =
    screenWidth > 1024
      ? [
          {
            Picture:
              'data:image/svg+xml;base64,' +
              cachedIcons?.MenueIcon['AnswerTestLab.svg'],
            Route: 'Lab',
            Title: lab_actions,
          },
          {
            Picture:
              'data:image/svg+xml;base64,' +
              cachedIcons?.MenueIcon[
                'Specializedlaboratory.svg'
              ],
            Route: 'Pato',
            Title: pato_title,
          },
        ]
      : [
          {
            Picture:
              'data:image/svg+xml;base64,' +
              cachedIcons?.MenueIcon['AnswerTestLab.svg'],
            Route: isOnlyPatient ? 'Lab' : '/Lab',
            Title: lab_actions,
          },
          {
            Picture:
              'data:image/svg+xml;base64,' +
              cachedIcons?.MenueIcon[
                'Specializedlaboratory.svg'
              ],
            Route: isOnlyPatient ? 'Pato' : '/Pato',
            Title: pato_title,
          },
        ];

  useEffect(() => {
    if (!Links) {
      return;
    }
    if (
      Links?.map(
        (option, i) =>
          AccessRoles?.map((role) =>
            role.toLowerCase()
          )?.includes(option.Route.toLowerCase()) ||
          AccessRoles?.map((role) =>
            role.toLowerCase().substring(1)
          )?.includes(option.Route.toLowerCase())
      )
    ) {
      const filtered_Routes = Links?.filter(
        (option, i) =>
          AccessRoles?.map((role) =>
            role.toLowerCase()
          )?.includes(option.Route.toLowerCase()) ||
          AccessRoles?.map((role) =>
            role.toLowerCase().substring(1)
          )?.includes(option.Route.toLowerCase())
      );

      setSideBarLinks(filtered_Routes);
    }
  }, [
    JSON.stringify(Links),
    JSON.stringify(AccessRoles),
    isTurnAllowed,
  ]);

  const patient_label = FindLangKey(
    'general-public-label-patient-role'
  );
  const doctor_label = FindLangKey(
    'general-public-label-doctor-role'
  );

  const doctorItems =
    Links &&
    Links?.filter(
      (option, i) =>
        (AccessRoles?.map((role) =>
          role.toLowerCase()
        )?.includes(option.Route.toLowerCase()) ||
          AccessRoles?.map((role) =>
            role.toLowerCase().substring(1)
          )?.includes(option.Route.toLowerCase())) &&
        !option?.IdPatient
    );
    const patientItems =
    Links &&
    Links?.filter(
      (option, i) =>
        (AccessRoles?.map((role) =>
          role.toLowerCase()
        )?.includes(option.Route.toLowerCase()) ||
          AccessRoles?.map((role) =>
            role.toLowerCase().substring(1)
          )?.includes(option.Route.toLowerCase())) &&
        option?.IdPatient)

  return {
    patient_label,
    doctor_label,
    doctorItems,
    responsiveGirds,
    isNestedRoute,
    personelWageLinks: personelWage_links,
    labLinks: labs_link,
    patientItems
  };
};

export default useLinkCreatorBl;
