import {
  ArrowDownward,
  Brush,
  ManageAccounts,
  PhonelinkSetup,
} from '@mui/icons-material';

import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import React, { useContext } from 'react';
import MainMenuDrawerOptions from '../MainMenuDrawer/components/MainMenuDrawerOptions';
import PatientAndDoctorDropDownGenerator from './PatientAndDoctorDropDownGenerator';
import useHandleSIdeBarAccordion_Bl from '../../Hooks/useHandleSIdeBarAccordion_Bl';
import SideBarAccordion from 'Global/Components/CommonComponents/SideBarAccordion/SideBarAccordion';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import SideBarContent from 'Global/Components/CommonComponents/SideBarAccordion/SideBarContent';
import { Stack } from '@mui/material';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import AccessControl from 'Global/security/AccessControl';
import { IconAtom } from 'Global/Hooks/useCacheIcons/IconAtom';
import { useAtom } from 'jotai';
import { findLast } from 'lodash';

const DesktopSideBarAccordionsForNonProfile = ({
  screenWidth,
  setDrawerOpen,
  setRefreshInterface,
  setRefreshForImage,
  isMobile,
}) => {
  const [icons] = useAtom(IconAtom);
  const cachedIcons =
    icons || JSON.parse(localStorage.getItem('icons'));

  const [expandedAccordion, setExpandedAccordion] =
    React.useState(false);
  const handleAccordionChange =
    (panel) => (event, isExpanded) => {
      setExpandedAccordion(isExpanded ? panel : false);
    };

  const {
    myProfile_label,
    patient_label,
    doctor_label,
    doctorItems,
    patientItems
  } = useHandleSIdeBarAccordion_Bl(isMobile, screenWidth);

  const setting_label = FindLangKey(
    'personalization-drawer-navbar-title'
  );

  const isDesktop = screenWidth > 1024;

  const GlobalConstants = useContext(GlobalContext);
  const isWebAppSettingsEnabled = AccessControl(
    'web-app-setting-accordion'
  );
  const AccessRoles = GlobalConstants?.AccessRoles;

  const personelWage_label = FindLangKey(
    'main-menu-devider-label-personel-wage'
  );

  const labs_label = FindLangKey('lab-patient-type-lab');
  const rootPaths = useContext(GlobalContext)?.rootPaths;

  const Settings =
    'data:image/svg+xml;base64,' +
    cachedIcons?.MenueIcon['Settings.svg'];
  const TakingTurns =
    'data:image/svg+xml;base64,' +
    cachedIcons?.MenueIcon['TakingTurns.svg'];
  const language =
    'data:image/svg+xml;base64,' +
    cachedIcons?.MenueIcon['language.svg'];


 
  return (
    <React.Fragment>
      <div
        style={{
          all: 'inherit',
        }}
        className="desktopAccordionsforNonProfile"
      >
        <SideBarAccordion
          pannelNo="panel1"
          handleAccordionChange={handleAccordionChange}
          expandedAccordion={expandedAccordion}
          label={myProfile_label}
        >
          <MainMenuDrawerOptions
            setDrawerOpen={setDrawerOpen}
            setRefreshInterface={setRefreshInterface}
            setRefreshForImage={setRefreshForImage}
            screenWidth={screenWidth}
          />
        </SideBarAccordion>

        {patientItems?.length > 0 ? (
          <SideBarAccordion
            pannelNo="panel2"
            handleAccordionChange={handleAccordionChange}
            expandedAccordion={expandedAccordion}
            label={doctor_label}
          >
            <PatientAndDoctorDropDownGenerator
              screenWidth={screenWidth}
              isMobile={isMobile}
            />
          </SideBarAccordion>
        ) : (
          <></>
        )}

        {doctorItems?.length !== 0 ? (
          <SideBarAccordion
            pannelNo="panel3"
            handleAccordionChange={handleAccordionChange}
            expandedAccordion={expandedAccordion}
            label={patient_label}
          >
            <PatientAndDoctorDropDownGenerator
              screenWidth={screenWidth}
              isDoctor={true}
              isMobile={isMobile}
            />
          </SideBarAccordion>
        ) : (
          <></>
        )}

        {doctorItems?.length !== 0 ? (
          <>
            {rootPaths?.includes('/InvoiceReceipt') ||
            rootPaths.includes('Quartering') ? (
              <SideBarAccordion
                pannelNo="panel5"
                handleAccordionChange={
                  handleAccordionChange
                }
                expandedAccordion={expandedAccordion}
                label={personelWage_label}
              >
                <Stack gap="1em">
                  {rootPaths?.includes(
                    '/InvoiceReceipt'
                  ) ? (
                    <SideBarContent
                      route={
                        isDesktop
                          ? '/main/InvoiceReceipt'
                          : '/InvoiceReceipt'
                      }
                      title={
                        'main-menu-button-invoice-receipt'
                      }
                      image={
                        'data:image/svg+xml;base64,' +
                        cachedIcons?.MenueIcon[
                          'InvoiceReceipt.svg'
                        ]
                      }
                    />
                  ) : (
                    <></>
                  )}

                  {rootPaths?.includes('/Quartering') ? (
                    <SideBarContent
                      route={
                        isDesktop
                          ? '/main/Quartering'
                          : '/Quartering'
                      }
                      title={'page-title-quartering'}
                      image={
                        'data:image/svg+xml;base64,' +
                        cachedIcons?.MenueIcon[
                          'CheckInout.svg'
                        ]
                      }
                    />
                  ) : (
                    <></>
                  )}
                </Stack>
              </SideBarAccordion>
            ) : (
              <></>
            )}
          </>
        ) : (
          <></>
        )}

        {doctorItems?.length !== 0 ? (
          <>
            {rootPaths?.includes('/Lab') ||
            rootPaths.includes('/Pato') ? (
              <SideBarAccordion
                pannelNo="panel6"
                handleAccordionChange={
                  handleAccordionChange
                }
                expandedAccordion={expandedAccordion}
                label={labs_label}
              >
                <Stack gap="1em">
                  {rootPaths?.includes('/Lab') ? (
                    <SideBarContent
                      route={
                        isDesktop ? '/main/Lab' : '/Lab'
                      }
                      title={'main-menu-button-lab-actions'}
                      image={
                        'data:image/svg+xml;base64,' +
                        cachedIcons?.MenueIcon[
                          'AnswerTestLab.svg'
                        ]
                      }
                    />
                  ) : (
                    <></>
                  )}
                  {rootPaths?.includes('/Pato') ? (
                    <SideBarContent
                      route={
                        isDesktop ? '/main/Pato' : '/Pato'
                      }
                      title={'patologoy-lab-route-title'}
                      image={
                        'data:image/svg+xml;base64,' +
                        cachedIcons?.MenueIcon[
                          'Specializedlaboratory.svg'
                        ]
                      }
                    />
                  ) : (
                    <></>
                  )}
                </Stack>
              </SideBarAccordion>
            ) : (
              <></>
            )}
          </>
        ) : (
          <></>
        )}

        <SideBarAccordion
          pannelNo="panel4"
          handleAccordionChange={handleAccordionChange}
          expandedAccordion={expandedAccordion}
          label={setting_label}
        >
          <Stack gap="1em">
            {isWebAppSettingsEnabled &&
            AccessRoles?.includes('admin') ? (
              <>
                <SideBarContent
                  route={
                    isDesktop
                      ? '/main/WebAppSettings'
                      : '/WebAppSettings'
                  }
                  title={
                    'genral-public-label-wep-app-settings'
                  }
                  icon={
                    <img
                      src={Settings}
                      alt={''}
                      width="25em"
                      height="25em"
                      style={{
                        background: Settings,
                        cursor: 'pointer',
                        backgroundPosition: 'center center',
                        backgroundSize: 'cover',
                      }}
                    />
                  }
                />

                <SideBarContent
                  route={
                    isDesktop
                      ? '/main/NobatPrefrences'
                      : '/NobatPrefrences'
                  }
                  title={
                    'main-menu-settings-drawer-nobat-prefrences'
                  }
                  icon={
                    <img
                      src={TakingTurns}
                      alt={''}
                      width="25em"
                      height="25em"
                      style={{
                        background: TakingTurns,
                        cursor: 'pointer',
                        backgroundPosition: 'center center',
                        backgroundSize: 'cover',
                      }}
                    />
                  }
                />
              </>
            ) : (
              <></>
            )}

            <SideBarContent
              route={
                isDesktop
                  ? '/main/Personalization'
                  : '/Personalization'
              }
              title={
                'genral-public-label-customization-settings'
              }
              icon={
                <img
                  src={language}
                  alt={''}
                  width="25em"
                  height="25em"
                  style={{
                    background: language,
                    cursor: 'pointer',
                    backgroundPosition: 'center center',
                    backgroundSize: 'cover',
                  }}
                />
              }
            />
          </Stack>
        </SideBarAccordion>
      </div>
    </React.Fragment>
  );
};

export default DesktopSideBarAccordionsForNonProfile;
