import React from "react";
import useLogOutUser from "../Hooks/useLogOutUser";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";
import { ExitToApp } from "@mui/icons-material";
const LogOutButton = ({ setDrawerOpen ,Exite}) => {
  const { LogUserOut } = useLogOutUser(setDrawerOpen);
  return (
    <React.Fragment>
      <span
        onClick={() => LogUserOut()}
        style={{
          display: "flex",
          flexDirection: "row-reverse",
          alignItems: "center",
          gap: ".5em",
          cursor: "pointer",
        }}
      >
           <img
            src={Exite}
            alt={FindLangKey("main-menu-drawer-link-log-out")}
            width="25em"
            height="25em"
            style={{
              background: Exite,
              cursor: 'pointer',
              backgroundPosition: 'center center',
              backgroundSize: 'cover',
            }}
          />
        {FindLangKey("main-menu-drawer-link-log-out")}
      </span>
    </React.Fragment>
  );
};

export default LogOutButton;
