import React, { useContext } from 'react';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import { fileSelector } from 'utils/FileSelector';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import {
  Button,
  ListItemIcon,
  MenuItem,
  MenuList,
  Stack,
} from '@mui/material';
import { Upload } from '@mui/icons-material';

const ImageUploader = ({
  setRefreshInterface,
  setRefreshForImage,
  cachedIcons
}) => {
  const fileInputRef = React.useRef(null);
  const GlobalConstants = useContext(GlobalContext);
  const operationDoneMsg = FindLangKey(
    'general-public-alert-success-operation'
  );
  const operatingMsg = FindLangKey(
    'general-public-alert-success-operation'
  );
  const maxSize_exceed_label = FindLangKey(
    'main-menu-dialog-max-size-exceeded-upload'
  );

  const UploadImg=  'data:image/svg+xml;base64,' +
  cachedIcons?.MenueIcon['UploadImg.svg'];

  return (
    <React.Fragment>
      <input
        onChange={(event) => {
          fileSelector(
            'image',
            setRefreshInterface,
            GlobalConstants,
            operationDoneMsg,
            setRefreshForImage,
            operatingMsg,
            maxSize_exceed_label
          )(event);
          event.target.value = null; // Reset the value
        }}
        type="file"
        accept=".jpg, .png, .jpeg, .gif, .bmp, .tif, .tiff|image/*"
        hidden
        ref={fileInputRef}
      />

      <span
        onClick={() => fileInputRef.current.click()}
        style={{
          display: 'flex',
          flexDirection: 'row-reverse',
          alignItems: 'center',
          gap: '.5em',
          cursor: 'pointer',
        }}
      >
          <img
            src={UploadImg}
            alt={FindLangKey(
              'main-menu-drawer-link-complete-profile'
            )}
            width="25em"
            height="25em"
            style={{
              background: UploadImg,
              cursor: 'pointer',
              backgroundPosition: 'center center',
              backgroundSize: 'cover',
            }}
          />
        {FindLangKey('main-menu-drawer-link-image-upload')}
      </span>
      {/* <Button
      
        sx={
          {
            // border: "1px solid gainsboro",
            // borderRadius: ".3em",
            // display: "flex",
            // flexDirection: "row-reverse",
            // justifyContent: "space-between",
            // pl: "1.2em",
          }
        }
        startIcon={}
        fullWidth
        color="colorPrimer"
      >
    
      </Button> */}
    </React.Fragment>
  );
};

export default ImageUploader;
