import React, { useEffect, useRef, useState } from 'react';
import {
  Box,
  Card,
  Stack,
  Alert,
  LinearProgress,
} from '@mui/material';
import { Links } from './variables/Links';
import MenuLinksCreator from 'Global/Components/Generators/MenuLinksCreator/MenuLinksCreator';
import styles from '../../Styles/Main.Menu.module.scss';
import { useContext } from 'react';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import useCheckScreenWidth from 'Global/Hooks/useCheckScreenWidth';
import ImageCarousel from '../ImageCarousel/ImageCarousel';
import useCallCarouselFetcher from '../../Hooks/useCallCarouselFetcher';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import Cookies from 'js-cookie';
import { DesktopLinks } from '../MainTopProfileSummary/DesktopLinks';
import useSetDefaultMenuSelection from '../../Hooks/useSetDefaultMenuSelection';
import NonCarousel from '../NonCarousel/NonCarousel';
import DesktopCarousel from '../DesktopCarousel/DesktopCarousel';
import { useLocation } from 'react-router-dom';
import useGetProfileImageOnMount from '../../Hooks/useGetProfileImageOnMount';
import { useAtom } from 'jotai';
import { ImageAtom } from '../../ImageAtom';
import AdTicker from '../AdTicker/AdTicker';
import GridItems from './components/GridItems';

const MainMenu = () => {
  const [forceRender, setForceRender] = useState(false);
  const IdPerson =
    Cookies.get('user') &&
    JSON.parse(Cookies.get('user'))?.IdPerson;

  const [, setRefreshForImage] = useAtom(ImageAtom);
  useGetProfileImageOnMount(setRefreshForImage);

  const GlobalConstants = useContext(GlobalContext);
  const rootPaths = useContext(GlobalContext)?.rootPaths;
  const isTurnAllowed =
    useContext(GlobalContext)?.isTurnAllowed;
  const lnks = DesktopLinks(isTurnAllowed, IdPerson);
  const roles = useContext(GlobalContext)?.roles;
  const screenWidth = useCheckScreenWidth(); // 1024 for Desktop
  const allLinks = Links(screenWidth, false, IdPerson);
  const images = useCallCarouselFetcher();

  const pathData = useLocation();
  const currentPath = pathData.pathname;

  const pathName = window.location.pathname;

  const no_access_label = FindLangKey(
    'genral-public-no-access-label'
  );

  const userCookie = Cookies.get('user');

  const userAccessRole =
    userCookie && JSON.parse(userCookie)?.AccessRole;

  useEffect(() => {
    if (pathName === '/main') {
      setForceRender((prev) => !prev);
      setForceRender((prev) => !prev);
      setForceRender((prev) => !prev);
      setForceRender((prev) => !prev);
    }
  }, [pathName]);
  return (
    <div
      style={{
        overflow: 'auto',

        paddingTop: screenWidth > 1024 ? '1em' : '0',
        marginTop: '0',
        display:
          screenWidth > 1900
            ? ''
            : userAccessRole?.includes('Patient') &&
              userAccessRole?.length === 1 &&
              screenWidth > 1024
            ? 'flex'
            : '',
        justifyContent:
          screenWidth > 1900
            ? ''
            : userAccessRole?.includes('Patient') &&
              userAccessRole?.length === 1 &&
              screenWidth > 1024
            ? 'space-evenly'
            : '',
        marginRight: screenWidth > 1900 ? '8em' : '',
        flexDirection: 'column',
        paddingBottom: '1em',
        overflowY: 'auto',
      }}
    >
      {/* {screenWidth > 1024 ? (
        <AdTicker
          headers={images?.List[0]?.Description}
          screenWidth={screenWidth}
        />
      ) : (
        <></>
      )} */}
      <Stack
        sx={{
          marginRight:
            screenWidth > 1024
              ? screenWidth > 1360
                ? '15em'
                : ''
              : '',
        }}
      >
        {screenWidth > 1024 && images?.List?.length > 0 ? (
          <DesktopCarousel
            images={images}
            screenWidth={screenWidth}
          />
        ) : (
          <></>
        )}
      </Stack>
      {/* {screenWidth > 1024 ? (
        <></>
      ) : (
        <AdTicker
          headers={images?.List[0]?.imageList?.map(
            (el) => el.Description
          )}
          screenWidth={screenWidth}
        />
      )} */}

      {screenWidth < 1024 ? (
        <ImageCarousel images={images} />
      ) : (
        <div style={{ paddingBlock: '1em' }}></div>
      )}
      <Stack className="main-menu-text">
        <Box>
          {forceRender ? (
            <GridItems
              userAccessRole={userAccessRole}
              allLinks={allLinks}
              screenWidth={screenWidth}
              rootPaths={rootPaths}
              isTurnAllowed={isTurnAllowed}
              lnks={lnks}
            />
          ) : (
            <GridItems
              userAccessRole={userAccessRole}
              allLinks={allLinks}
              screenWidth={screenWidth}
              rootPaths={rootPaths}
              isTurnAllowed={isTurnAllowed}
              lnks={lnks}

            />
          )}
        </Box>
        {screenWidth < 1024 ? (
          <Stack>
            {/* <ImageCarousel images={images} /> */}
            <NonCarousel
              images={images?.List.filter(
                (fl) => !fl.ShowInBanner
              )}
            />
          </Stack>
        ) : (
          <></>
        )}
      </Stack>
      <br />
    </div>
  );
};

export default MainMenu;

