import React, { useEffect, useState } from 'react';

const useClock = (showTime = true) => {
  const [currentTime, setCurrentTime] = useState(
    new Date()
  );

  useEffect(() => {
    const intervalId = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);

    return () => clearInterval(intervalId);
  }, []);

  const dateFormatter = new Intl.DateTimeFormat('fa-IR', {
    year: 'numeric',
    month: 'long',
    day: 'numeric',
    weekday: 'long',
  });

  const formatDate = (date) => {
    const parts = dateFormatter.formatToParts(date);
    const weekday = parts.find(
      (part) => part.type === 'weekday'
    ).value;
    const day = parts.find(
      (part) => part.type === 'day'
    ).value;
    const month = parts.find(
      (part) => part.type === 'month'
    ).value;
    const year = parts.find(
      (part) => part.type === 'year'
    ).value;

    return `${weekday} ${day} ${month} ${year}`;
  };

  const formatTime = (date) => {
    const hours = date
      .getHours()
      .toString()
      .padStart(2, '0');
    const minutes = date
      .getMinutes()
      .toString()
      .padStart(2, '0');
    return `${hours}:${minutes}`;
  };

  const formattedDate = formatDate(currentTime);
  const formattedTime = formatTime(currentTime);

  const formattedResultWithTime = ` ${formattedDate}  ${formattedTime}`;
  const formattedResultWithoutTime = ` ${formattedDate}`;

  return showTime
    ? formattedResultWithTime
    : formattedResultWithoutTime;
};

export default useClock;
