import React, { useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import styles from '../../../Styles/Menu.Options.module.scss';
import ComponentMemoizer from 'Global/Components/ComponentMemoizer/ComponentMemoizer';
import { sideBarLinks } from 'Routes/Protected/MainPage/shared/side.bar.links';
import { useAtom } from 'jotai';
import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import FrameCard from 'Global/Components/CommonComponents/FrameCard/FrameCard';

const DoctorItemsDesktop = (props) => {
  const {
    screenWidth,
    Links,
    AccessRoles,
    isTurnAllowed,
    isNestedRoute,
  } = props;

  return (
    <React.Fragment>
      {screenWidth > 1024 ? (
        <div
          style={{
            flexShrink: '1',
          }}
        >
          <ComponentMemoizer
            element={Links?.map(
              (option, i) =>
                (AccessRoles?.map((role) =>
                  role.toLowerCase()
                )?.includes(option.Route.toLowerCase()) ||
                  AccessRoles?.map((role) =>
                    role.toLowerCase().substring(1)
                  )?.includes(
                    option.Route.toLowerCase()
                  )) &&
                !option?.IdPatient && (
                  <React.Fragment key={i}>
                    <Link
                      to={
                        [
                          '/TurnList',
                          '/GetNewTurn',
                        ].includes(option.Route)
                          ? !isTurnAllowed
                            ? ''
                            : option.Route
                          : option.Route
                      }
                      replace
                      style={{
                        textDecoration: 'none',
                      }}
                    >
                      <FrameCard
                        cardProps={{
                          p: '.2em',
                          borderRadius: '20px',
                          minWidth: '12em !important',
                          display: 'flex',
                          justifyContent: 'flex-start',
                        }}
                        jsx={
                          <section
                            style={{
                              backgroundColor: 'white',
                              borderRadius: '20px',
                              width: '100%',
                              display: 'flex',
                              justifyContent: 'center',
                              minHeight: '7em',
                              fontSize: '1.2em',
                              alignItems: 'center',
                              gap: '.5em',
                            }}
                            className={styles.eachLink}
                            onClick={() => {
                              if (
                                [
                                  '/TurnList',
                                  '/GetNewTurn',
                                ].includes(option.Route) &&
                                !isTurnAllowed
                              ) {
                                SwalAlert(
                                  'warning',
                                  cant_get_newTurn_label,
                                  false,
                                  false,
                                  false,
                                  false,
                                  false,
                                  true
                                );
                              }
                            }}
                          >
                            <img
                              src={option.Picture}
                              alt={option.Title}
                              loading="..."
                              style={{
                                background: option.Picture,
                                cursor: 'pointer',
                                backgroundPosition:
                                  'center center',
                                backgroundSize: 'cover',
                                filter: [
                                  '/TurnList',
                                  '/GetNewTurn',
                                ].includes(option.Route)
                                  ? !isTurnAllowed
                                    ? 'grayscale(1)'
                                    : 'unset'
                                  : 'unset',
                              }}
                              width={
                                screenWidth >= 1440 &&
                                isNestedRoute
                                  ? 60
                                  : !isNestedRoute
                                  ? 60
                                  : 40
                              }
                              height={
                                screenWidth >= 1440 &&
                                isNestedRoute
                                  ? 60
                                  : !isNestedRoute
                                  ? 60
                                  : 40
                              }
                            />

                            <p
                              style={{
                                fontSize: '0.8125em',
                              }}
                            >
                              {option.Title}
                            </p>
                          </section>
                        }
                      />
                    </Link>
                  </React.Fragment>
                )
            )}
            dependecies={[
              localStorage.getItem('icons'),
              isTurnAllowed,
            ]}
          />
        </div>
      ) : (
        <ComponentMemoizer
          element={Links?.map(
            (option, i) =>
              (AccessRoles?.map((role) =>
                role.toLowerCase()
              )?.includes(option.Route.toLowerCase()) ||
                AccessRoles?.map((role) =>
                  role.toLowerCase().substring(1)
                )?.includes(option.Route.toLowerCase())) &&
              !option?.IdPatient && (
                <React.Fragment key={i}>
                  <Link
                    to={
                      ['/TurnList', '/GetNewTurn'].includes(
                        option.Route
                      )
                        ? !isTurnAllowed
                          ? ''
                          : option.Route
                        : option.Route
                    }
                    replace
                    style={{
                      textDecoration: 'none',
                    }}
                  >
                    <FrameCard
                      cardProps={{
                        p: '.2em',
                        borderRadius: '20px',
                        minWidth: '12em !important',
                        display: 'flex',
                        justifyContent: 'flex-start',
                      }}
                      jsx={
                        <section
                          style={{
                            backgroundColor: 'white',
                            borderRadius: '20px',
                            width: '100%',
                            display: 'flex',
                            justifyContent: 'center',
                            minHeight: '7em',
                            fontSize: '1.2em',
                            alignItems: 'center',
                            gap: '.5em',
                          }}
                          className={styles.eachLink}
                          onClick={() => {
                            if (
                              [
                                '/TurnList',
                                '/GetNewTurn',
                              ].includes(option.Route) &&
                              !isTurnAllowed
                            ) {
                              SwalAlert(
                                'warning',
                                cant_get_newTurn_label,
                                false,
                                false,
                                false,
                                false,
                                false,
                                true
                              );
                            }
                          }}
                        >
                          <img
                            src={option.Picture}
                            alt={option.Title}
                            loading="..."
                            style={{
                              background: option.Picture,
                              cursor: 'pointer',
                              backgroundPosition:
                                'center center',
                              backgroundSize: 'cover',
                              filter: [
                                '/TurnList',
                                '/GetNewTurn',
                              ].includes(option.Route)
                                ? !isTurnAllowed
                                  ? 'grayscale(1)'
                                  : 'unset'
                                : 'unset',
                            }}
                            width={
                              screenWidth >= 1440 &&
                              isNestedRoute
                                ? 60
                                : !isNestedRoute
                                ? 60
                                : 40
                            }
                            height={
                              screenWidth >= 1440 &&
                              isNestedRoute
                                ? 60
                                : !isNestedRoute
                                ? 60
                                : 40
                            }
                          />

                          <p
                            style={{ fontSize: '0.8125em' }}
                          >
                            {option.Title}
                          </p>
                        </section>
                      }
                    />
                  </Link>
                </React.Fragment>
              )
          )}
          dependecies={[
            localStorage.getItem('icons'),
            isTurnAllowed,
          ]}
        />
      )}
    </React.Fragment>
  );
};

export default DoctorItemsDesktop;
