import React, { useContext, useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import ComponentMemoizer from 'Global/Components/ComponentMemoizer/ComponentMemoizer';
import { sideBarLinks } from 'Routes/Protected/MainPage/shared/side.bar.links';
import { useAtom } from 'jotai';
import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import PatientItems from './PatientItems';
import DoctorItems from './DoctorItems';
import styles from '../../../../Routes/Protected/MainPage/Styles/Main.Menu.module.scss';
import { Box, Container, Stack } from '@mui/material';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import Cookies from 'js-cookie';
import useLinkCreatorBl from 'Global/Hooks/useLinkCreatorBl';

const MenuLinksCreator = ({
  Links,
  AccessRoles,
  isTurnAllowed,
  screenWidth,
}) => {
  const[forceRender,setForceRender] = useState(false);
  const {
    patient_label,
    doctor_label,
    doctorItems,
    responsiveGirds,
    isNestedRoute,
    personelWageLinks,
    labLinks,
    patientItems
  } = useLinkCreatorBl(
    screenWidth,
    Links,
    AccessRoles,
    isTurnAllowed
  );

  const personelWage_label = FindLangKey(
    'main-menu-devider-label-personel-wage'
  );
  const labsCategory_label = FindLangKey(
    'lab-patient-type-lab'
  );

  const rootPaths = useContext(GlobalContext)?.rootPaths;

  const isPersonelWageAccessible =
    rootPaths?.includes('/InvoiceReceipt') ||
    rootPaths.includes('Quartering');

  const isLabAccessible =
    rootPaths?.includes('/Lab') ||
    rootPaths.includes('/Pato');



  useEffect(()=>{
    setForceRender(prev=>!prev);
    setForceRender(prev=>!prev);
    setForceRender(prev=>!prev);
    setForceRender(prev=>!prev);
    setForceRender(prev=>!prev);
  },[])
 
 
  return (
    <Stack
      marginTop={screenWidth >= 1024 ? '1em' : ''}
      marginRight={screenWidth >= 1024 ? '14.5em' : ''}
      sx={{
        paddingInline: screenWidth > 1024 ? '2em' : '',
      }}
    >
      {patientItems?.length > 0 ? (
        <Container sx={{ mb: '2em' }}>
          <Stack
            direction="row"
            gap=".5em"
            alignItems="center"
          >
            <hr
              style={{
                width: '100%',
                padding: 0,
                height: '1px',
              }}
            />
            <p>{patient_label}</p>
          </Stack>
        </Container>
      ) : (
        <br />
      )}
      {forceRender?<p></p>:<></>}
      {screenWidth > 1024 ? <br /> : <></>}

      <Box
        className={styles.MenuOptions}
        sx={responsiveGirds}
      >
        <PatientItems
          Links={Links}
          AccessRoles={AccessRoles}
          isTurnAllowed={isTurnAllowed}
          isNestedRoute={isNestedRoute}
          screenWidth={screenWidth}
        />
              {forceRender?<p></p>:<></>}

      </Box>

      {doctorItems?.length > 0 ? (
        <Container sx={{ my: '2em' }}>
          <Stack
            direction="row"
            gap=".5em"
            alignItems="center"
          >
            <hr
              style={{
                width: '100%',
                padding: 0,
                height: '1px',
              }}
            />
            <p>{doctor_label}</p>
          </Stack>
        </Container>
      ) : (
        <></>
      )}
      {screenWidth > 1024 ? <br /> : <></>}
      <Box
        className={styles.MenuOptions}
        sx={responsiveGirds}
      >
        <DoctorItems
          Links={Links}
          AccessRoles={AccessRoles}
          isTurnAllowed={isTurnAllowed}
          isNestedRoute={isNestedRoute}
          screenWidth={screenWidth}
        />
              {forceRender?<p></p>:<></>}

      </Box>

      {doctorItems?.length > 0 &&
      isPersonelWageAccessible ? (
        <Container sx={{ my: '2em' }}>
          <Stack
            direction="row"
            gap=".5em"
            alignItems="center"
          >
            <hr
              style={{
                width: '100%',
                padding: 0,
                height: '1px',
              }}
            />
            <p
              style={{
                width:
                  screenWidth > 1024 ? '7.5em' : '10em',
              }}
            >
              {personelWage_label}
            </p>
          </Stack>
        </Container>
      ) : (
        <></>
      )}
      {screenWidth > 1024 ? <br /> : <></>}
      <Box
        className={styles.MenuOptions}
        sx={responsiveGirds}
      >
        <DoctorItems
          Links={personelWageLinks}
          AccessRoles={AccessRoles}
          isTurnAllowed={isTurnAllowed}
          isNestedRoute={isNestedRoute}
          screenWidth={screenWidth}
        />
      </Box>

      {doctorItems?.length > 0 && isLabAccessible ? (
        <Container sx={{ my: '2em' }}>
          <Stack
            direction="row"
            gap=".5em"
            alignItems="center"
          >
            <hr
              style={{
                width: '100%',
                padding: 0,
                height: '1px',
              }}
            />
            <p
              style={{
                width:
                  screenWidth > 1024 ? '7.5em' : '10em',
              }}
            >
              {labsCategory_label}
            </p>
          </Stack>
        </Container>
      ) : (
        <></>
      )}
      {screenWidth > 1024 ? <br /> : <></>}
      <Box
        className={styles.MenuOptions}
        sx={responsiveGirds}
      >
        <DoctorItems
          Links={labLinks}
          AccessRoles={AccessRoles}
          isTurnAllowed={isTurnAllowed}
          isNestedRoute={isNestedRoute}
          screenWidth={screenWidth}
        />
      </Box>
      {forceRender?<p></p>:<></>}

    </Stack>
  );
};

export default MenuLinksCreator;
