import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';

export const DrawerLinks = (
  isPatient,
  rootPaths,
  screenWidth,
  isDesktop,
  cachedIcons
) => {
  // Precompute the outputs of FindLangKey
  const langKeys = {
    completeProfile: FindLangKey('main-menu-drawer-link-complete-profile'),
    uploadDocs: FindLangKey('main-menu-drawer-link-upload-on-call-docs'),
    bankAccountList: FindLangKey('bank-account-list-navbar-title'),
    walletButton: FindLangKey('main-menu-drawer-wallet-button'),
    changePassword: FindLangKey('general-public-change-password'),
    userPishNahad: FindLangKey('user-pishnahad-route-title'),
  
  };

  const icons = {
    completeProfileInfo:
      'data:image/svg+xml;base64,' + cachedIcons?.MenueIcon['Completioninformation.svg'],
    uploadDocs:
      'data:image/svg+xml;base64,' + cachedIcons?.MenueIcon['UploadDocs.svg'],
    changePassword:
      'data:image/svg+xml;base64,' + cachedIcons?.MenueIcon['changePassword.svg'],
    criticism:
      'data:image/svg+xml;base64,' + cachedIcons?.MenueIcon['Criticism.svg'],
    wallet:
      'data:image/svg+xml;base64,' + cachedIcons?.MenueIcon['Wallet.svg'],
    listehesab:
      'data:image/svg+xml;base64,' + cachedIcons?.MenueIcon['Listhesab.svg'],
  };

  const links = [
    {
      Picture: (
        <img
          src={icons.completeProfileInfo}
          alt={langKeys.completeProfile}
          width="25em"
          height="25em"
          style={{
            background: icons.completeProfileInfo,
            cursor: 'pointer',
            backgroundPosition: 'center center',
            backgroundSize: 'cover',
          }}
        />
      ),
      Route: isDesktop ? '/main/CompleteProfileUser' : '/CompleteProfileUser',
      Title: langKeys.completeProfile,
    },
    {
      Picture: (
        <img
          src={icons.uploadDocs}
          alt={langKeys.uploadDocs}
          width="25em"
          height="25em"
          style={{
            background: icons.uploadDocs,
            cursor: 'pointer',
            backgroundPosition: 'center center',
            backgroundSize: 'cover',
          }}
        />
      ),
      Route: isDesktop ? '/main/UploadOnCallDocs' : '/UploadOnCallDocs',
      Title: langKeys.uploadDocs,
    },
    {
      Picture: (
        <img
          src={icons.listehesab}
          alt={langKeys.bankAccountList}
          width="25em"
          height="25em"
          style={{
            background: icons.listehesab,
            cursor: 'pointer',
            backgroundPosition: 'center center',
            backgroundSize: 'cover',
          }}
        />
      ),
      Route: isDesktop ? '/main/BankAccountList' : '/BankAccountList',
      Title: langKeys.bankAccountList,
    },
    rootPaths?.includes('/Wallet') && {
      Picture: (
        <img
          src={icons.wallet}
          alt={langKeys.walletButton}
          width="25em"
          height="25em"
          style={{
            background: icons.wallet,
            cursor: 'pointer',
            backgroundPosition: 'center center',
            backgroundSize: 'cover',
          }}
        />
      ),
      Route: isDesktop ? '/main/Wallet' : '/Wallet',
      Title: langKeys.walletButton,
      isUnAuth: true,
    },
    {
      Picture: (
        <img
          src={icons.changePassword}
          alt={langKeys.changePassword}
          width="25em"
          height="25em"
          style={{
            background: icons.changePassword,
            cursor: 'pointer',
            backgroundPosition: 'center center',
            backgroundSize: 'cover',
          }}
        />
      ),
      Route: isDesktop ? '/main/ChangePasswrod' : '/ChangePasswrod',
      Title: langKeys.changePassword,
    },
    
    rootPaths?.includes('/PatientPishNahad') && {
      Picture: (
        <img
          src={icons.criticism}
          alt={langKeys.userPishNahad}
          width="25em"
          height="25em"
          style={{
            background: icons.criticism,
            cursor: 'pointer',
            backgroundPosition: 'center center',
            backgroundSize: 'cover',
          }}
        />
      ),
      Route: isDesktop ? '/main/PatientPishNahad' : '/PatientPishNahad',
      Title: langKeys.userPishNahad,
    },
  ];

  // Ensure only valid links are returned
  return links.filter((link) => link);
};
