import React from 'react';
import { GetCarouselImage } from '../Adapters/GetCarouselImage';
import useCacheCarouselImages from './useCacheCarouselImages';

const useCallCarouselFetcher = () => {
  const areImagesCached =
    localStorage.getItem('carouselCached') &&
    JSON.parse(localStorage.getItem('carouselCached'));

  const [images, setCarouselImages] = React.useState(()=>    localStorage.getItem('carouselCached') &&
  JSON.parse(localStorage.getItem('carouselCached')));

  const cachedImages = useCacheCarouselImages(images?.List);

  // console.log('cachedImages=>', cachedImages);

  React.useEffect(() => {
    if (!areImagesCached) {
      GetCarouselImage().then((response) => {
        setCarouselImages(response.data.Data);
      });
    }
  }, [areImagesCached]);

  return cachedImages;
};

export default useCallCarouselFetcher;
