import { Info } from '@mui/icons-material';
import { CssBaseline, Stack } from '@mui/material';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import React, { useContext } from 'react';
import PatientDoctor from './PatientDoctor';
import { DesktopLinks } from './DesktopLinks';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import Cookies from 'js-cookie';
import { Links as MobileLinks } from '../MainMenu/variables/Links';
import { v4 as uuidV4 } from 'uuid';

const PatientAndDoctorDropDownGenerator = ({
  screenWidth,
  isDoctor,
  isMobile,
}) => {
  const IdPerson =
    Cookies.get('user') &&
    JSON.parse(Cookies.get('user'))?.IdPerson;
  const isTurnAllowed =
    useContext(GlobalContext)?.isTurnAllowed;
  const Links = isMobile
    ? MobileLinks(screenWidth, false, IdPerson)
    : DesktopLinks(isTurnAllowed, IdPerson);

  const AccessRoles = useContext(GlobalContext)?.rootPaths;

  return (
    <React.Fragment>
      <CssBaseline />
      <Stack gap="1.5em">
        {Links?.map(
          (option, i) =>
            (AccessRoles?.map((role, j) =>
              role.toLowerCase()
            )?.includes(option.Route.toLowerCase()) ||
              AccessRoles?.map((role) =>
                role.toLowerCase().substring(1)
              )?.includes(option.Route.toLowerCase())) &&
            (isDoctor
              ? !option?.IdPatient
              : option?.IdPatient) && (
              <PatientDoctor
                title={option.Title}
                route={option.Route}
                image={option.Picture}
                key={uuidV4()}
              />
            )
        )}
      </Stack>
    </React.Fragment>
  );
};

export default PatientAndDoctorDropDownGenerator;
