import React, { useContext } from 'react';
import { Container, Stack } from '@mui/material';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import { useAtom } from 'jotai';
import sideBarIconStyles from './sidebar.icons.colors.module.scss';
 import { InterfaceAtom } from '../../InterfaceAtom';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import { ImageAtom } from '../../ImageAtom';
import {
  AccountCircle,
  ArrowDownward,
} from '@mui/icons-material';
import Cookies from 'js-cookie';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import ImageTitlePack from '../MainMenuDrawer/components/ImageTitlePack';
import useCheckScreenWidth from 'Global/Hooks/useCheckScreenWidth';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import DesktopSideBarAccordionsForNonProfile from './DesktopSideBarAccordionsForNonProfile';
import ComponentMemoizer from 'Global/Components/ComponentMemoizer/ComponentMemoizer';

const DesktopSideBar = () => {
  const imgHeader = 'data:image/svg+xml;base64,';

  const userImage =
    localStorage.getItem('userImage') !== null &&
    JSON.parse(localStorage.getItem('userImage'));

  const cachedIcons = JSON.parse(
    localStorage.getItem('icons')
  );

  const [, setRefreshInterface] = useAtom(InterfaceAtom);

  const [, setRefreshForImage] = useAtom(ImageAtom);

  const GlobalConstants =
    useContext(GlobalContext)?.setDrawerOpen;

  const setDrawerOpen = GlobalConstants.setDrawerOpen;

  const userName =
    Cookies.get('user') !== undefined &&
    JSON.parse(Cookies.get('user'));

  const screenWidth = useCheckScreenWidth(); // 1024 for Desktop

  return (
    <React.Fragment>
      <Container
        sx={{
          position: 'relative',
          overflowX: 'hidden !important',
          p: '.5em !important',
          height: '100vh',
          
        }}
      >
        <Stack sx={{ mt: '1.5em', }} alignItems="center">
          <ComponentMemoizer element={<>
            
             {userImage?.length > 0 ? (
              <img
                src={`data:image/*;base64,${userImage}`}
                alt=" "
                loading="..."
                width={75}
                height={75}
                style={{ borderRadius: '.3em',border:`1px solid ${themeColors.ColorPrimary}` }}
              />
            ) : (
              <AccountCircle
                sx={{
                  width: '6em',
                  height: '6em',
                }}
              />
            )
          }
            </>} dependecies={[userImage,themeColors]}/>
          <p
            style={{
              color: `${themeColors.ColorPrimary}`,
              marginTop:'1em'
            }}
          >
            {ArabicToPersian(userName?.FullName)}
          </p>
          <ImageTitlePack userName={userName} />

          <br />
          <div
            style={{
              minWidth: '15em',
            }}
          >
            {/* className={sideBarIconStyles.container} */}

            <Stack>
              <DesktopSideBarAccordionsForNonProfile
                screenWidth={screenWidth}
                setDrawerOpen={setDrawerOpen}
                setRefreshInterface={setRefreshInterface}
                setRefreshForImage={setRefreshForImage}
              />
            </Stack>
          </div>
        </Stack>
      </Container>
    </React.Fragment>
  );
};

export default DesktopSideBar;
