import React from 'react';
import { Stack } from '@mui/material';

import Cookies from 'js-cookie';
import ImageTitlePack from './ImageTitlePack';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import { AccountCircle } from '@mui/icons-material';

const MenuDrawerSummary = ({ styles }) => {
  const userName =
    Cookies.get('user') !== undefined &&
    JSON.parse(Cookies.get('user'));
  const userImage =
    localStorage.getItem('userImage') !== null &&
    JSON.parse(localStorage.getItem('userImage'));

  const cachedIcons = JSON.parse(
    localStorage.getItem('icons')
  );
  const imgHeader = 'data:image/svg+xml;base64,';

  return (
    <React.Fragment>
      <Stack mt=".5em">
        <Stack alignItems="center" gap=".3em">
          {userImage?.length > 0 ? (
            <img
              src={`data:image/*;base64,${userImage}`}
              alt=" "
              loading="..."
              width={75}
              height={75}
              style={{ borderRadius: '.3em',border:`1px solid ${themeColors.ColorPrimary}` }}
            />
          ) : (
            <AccountCircle
              sx={{
                width: '6em',
                height: '6em',
                fill: `${themeColors.ColorSecondary} !important`,
              }}
            />
          )}

          <p
            style={{
              color: `${themeColors.ColorSecondary} !important`,
            }}
          >
            {ArabicToPersian(userName?.FullName)}
          </p>
        </Stack>

        <ImageTitlePack userName={userName} />
      </Stack>
    </React.Fragment>
  );
};

export default MenuDrawerSummary;
