import React, { useContext } from 'react';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';

const DrawerMenuLinkCreator = ({ Links, AccessRoles }) => {
  const navigate = useContext(GlobalContext)?.navigate;

  const isAccessAllowed = (option, roles) => {
    const lowerCaseRoles = roles.map((role) =>
      role.toLowerCase()
    );

    const nestedRoles = roles.map((role) => `/main${role}`);

    return (
      option.isUnAuth ||
      lowerCaseRoles.includes(option.Route.toLowerCase()) ||
      nestedRoles.includes(option.Route)
    );
  };

  return (
    <React.Fragment>
      {Links?.map(
        (option, i) =>
          isAccessAllowed(option, AccessRoles) && (
            <React.Fragment key={i}>
              <span
                onClick={() => navigate(option.Route)}
                style={{
                  display: 'flex',
                  flexDirection: 'row-reverse',
                  alignItems: 'center',
                  gap: '.5em',
                  cursor: 'pointer',
                }}
              >
                {React.cloneElement(option.Picture, {
                  sx: {
                    fontSize: '1.5em !important',
                  },
                  color: 'colorPrimer',
                })}
                {option.Title}
              </span>
            </React.Fragment>
          )
      )}
    </React.Fragment>
  );
};

export default DrawerMenuLinkCreator;
